<template>
  <div class="groupRecruitment">
    <h3>-</h3>
    <ol v-for="Group in recGroups">
      <ul>
        <div class="Content">
          <div class="Logo pull-left">
            <img v-bind:src="Group.img">
          </div>
          <div class="pull-right">
            <li class="list-group-item glyphicon glyphicon-bookmark"> :{{Group.competition}}</li>
            <li class="list-group-item glyphicon glyphicon-star-empty"> :{{Group.name}}</li>
            <li class="list-group-item glyphicon glyphicon-user"> :{{Group.originator}}</li>
            <li class="list-group-item glyphicon glyphicon-calendar"> :{{Group.time}}</li>
            <li class="list-group-item glyphicon glyphicon-bullhorn"> :{{Group.memberNum}}</li>
            <li class="list-group-item glyphicon glyphicon-bell" v-if="Group.notEnough"> :{{Group.deadline}}</li>
          </div>
        </div>
      </ul>
    </ol>
  </div>
</template>


<script>
  export default {
    name: 'groupRecruitment',
    data(){
      return{
        recGroups: [
          {
            img: './static/img/.png',
            name: ' -',
            competition: ' 2018',
            time: ' 2017/12/23',
            originator: ' ',
            memberNum: ' 34/-',
            deadline: ' -',
            notEnough: false
           },
          {
            img: './static/img/.png',
            name: ' ad-hoc PM Intern',
            competition: ' ',
            time: ' 2017/12/27',
            originator: ' IM China',
            memberNum: ' -/3',
            deadline: ' 2017/12/20',
            notEnough: true
          }
        ]
      }
    }
  }
</script>

<style>

.Content{
  margin:auto;
  bottom: 60px;
  right: 300px;
  width: 500px;
  height: 200px;
  text-align:left;
}
.Logo{
  margin: auto;
  position: relative;
  top: 20px;
  right: 10px;
  width: 110px;
  height: 130px;
}
.Logo img{
  display:block;
  outline:none;
  border:0;
  height:100%;
  width:100%;
}
</style>



