<template>
  <div class="groupRecommend">
    <h3>-</h3>
    <ol v-for="Group in actGroups">
      <ul>
        <div class="Content">
          <div class="Logo pull-left">
            <img v-bind:src="Group.img">
          </div>
          <div class="pull-right">
            <li class="list-group-item glyphicon glyphicon-star-empty"> :{{Group.name}}</li>
            <li class="list-group-item glyphicon glyphicon-bookmark"> :{{Group.competition}}</li>
            <li class="list-group-item glyphicon glyphicon-calendar"> :{{Group.time}}</li>
            <li class="list-group-item glyphicon glyphicon-user"> :{{Group.originator}}</li>
            <li class="list-group-item glyphicon glyphicon-bullhorn"> :{{Group.memberNum}}</li>
            <li class="list-group-item glyphicon glyphicon-bell" v-if="Group.notEnough"> :{{Group.deadline}}</li>
          </div>
        </div>
      </ul>
    </ol>
  </div>
</template>


<script>
  export default {
    name: 'groupRecommend',
    data(){
      return{
        actGroups: [
          {
            img: './static/img/.png',
            name: ' ',
            competition: ' ',
            time: '2017/12/19 13:00',
            originator: ' ',
            memberNum: ' 2/3',
            deadline: ' 2017/12/18',
            notEnough: true
           },
          {
            img: './static/img/.png',
            name: ' ',
            competition: ' ',
            time: ' 2017/12/22 12:00',
            originator: ' ninini',
            memberNum: ' 6/-',
            deadline: ' 2017/12/20 1530',
            notEnough: true
          }
        ]
      }
    }
  }
</script>
<style>
.Content{
  margin:auto;
  bottom: 60px;
  right: 300px;
  width: 470px;
  height: 200px;
  text-align:left;
}
.Logo{
  margin: auto;
  position: relative;
  top: 20px;
  right: 10px;
  width: 100px;
  height: 140px;
}
.Logo img{
  display:block;
  outline:none;
  border:0;
  height:100%;
  width:100%;
}
</style>



