<template>
  <div class="category">
    <h3></h3>
    <div class="panel panel-default" style="width: 50% ;margin:auto;">
      <div class="panel-body">
        <ol v-for="group in category">
          <nav class="navbar navbar-default" role="navigation">
            <div class="container-fluid">
              <div class="navbar-header">
                <a class="navbar-brand" href="#">{{group.time}}</a>
              </div>
              <div>
                <ul class="nav navbar-nav">
                  <li v-bind:class="{ active: isActive_1 }"><a href="#" v-on:click="show">{{group.name1}}</a></li>
                  <li v-bind:class="{ active: isActive_2 }" ><a href="#" v-on:click="show_2">{{group.name2}}</a></li>
                  <li v-bind:class="{ active: isActive_3 }"><a href="#" v-on:click="show_3">{{group.name3}}</a></li>
                  <li v-bind:class="{ active: isActive_4 }" ><a href="#" v-on:click="show_4">{{group.name4}}<span class="badge" >hot</span></a></li>
                </ul>
              </div>
            </div>
          </nav>

        </ol>
        <ol v-for="group in category2">
          <nav class="navbar navbar-default" role="navigation">
            <div class="container-fluid">
              <div class="navbar-header">
                <a class="navbar-brand" href="#">{{group.time}}</a>
              </div>
              <div>
                <ul class="nav navbar-nav">
                  <li v-bind:class="{ active: isActive_5 }"><a href="#" v-on:click="show_5">{{group.name1}}</a></li>
                  <li v-bind:class="{ active: isActive_6 }" ><a href="#" v-on:click="show_6">{{group.name2}}</a></li>
                  <li v-bind:class="{ active: isActive_7 }"><a href="#" v-on:click="show_7">{{group.name3}}</a></li>
                  <li v-bind:class="{ active: isActive_8 }" ><a href="#" v-on:click="show_8">{{group.name4}}</a></li>
                </ul>
              </div>
            </div>
          </nav>
        </ol>
        <div class="progress" style="width:50% ;float:left">
          <div class="progress-bar" role="progressbar" aria-valuenow="60"
               aria-valuemin="0" aria-valuemax="100" style="width:60%">
            <span>60%</span>
          </div>
        </div>
        <ol v-for="group in labell">
          <span class="label label-primary">:{{cclick}}</span>
          <span class="label label-success">:{{cstar}}</span>
          <span class="label label-info">:{{group.time}}</span>
        </ol>
      </div>
    </div>
    <div class="cshow" v-show="cshow">
    <group-competition></group-competition>
    </div>
    <div class="cshow1" v-show="cshow1">
      <br>
      <h3> </h3>
    </div>
  </div>
</template>

<script>
  import GroupCompetition from 'components/groups/groupCompetition/groupCompetition'
  export default {
    name: 'categoryCompetition',
    components: {
        GroupCompetition
    },
    data(){

        return {
          cshow:true,
          cshow1:false,
          isActive_1:true,
          isActive_2:false,
          isActive_3:false,
          isActive_4:false,
          isActive_5:false,
          isActive_6:false,
          isActive_7:false,
          isActive_8:false,
          cclick:0,
          cstar:0,
          category: [
            {
              name1: '',
              name2: '      ',
              name3: '',
              name4: '     ',
              time: '',
              jindu: "70%",
              n_jindu: "70%"
            }
          ],
          category2: [
            {
              name1: '',
              name2: '',
              name3: '',
              name4: '',
              time: '',
              jindu: '50%',
              n_jindu: "50%"
            }
          ],
          labell: [
            {
              sort: '',
              click: '233',
              star: '233',
              time: '2017/11/11',
              people: '233'
            }
          ],
        }
    },
    methods: {
      show: function () {
        // `this`  Vue 

        // `event`  DOM 
        this.cclick = this.cclick+1;
        this.cstar= this.cstar+1;
        this .cshow = true;
        this .cshow1 = false;
        this.isActive_1 = true;
        this.isActive_2 = false;
        this.isActive_3 = false;
        this.isActive_4 = false;
      },
      show_2: function () {
        // `this`  Vue 

        // `event`  DOM 
        this.cclick = this.cclick+1;
        this.cstar= this.cstar+1;
        this .cshow = false;
        this .cshow1 = true;
        this.isActive_1 = false;
        this.isActive_2 = true;
        this.isActive_3 = false;
        this.isActive_4 = false;
      },
      show_3: function () {
        // `this`  Vue 

        // `event`  DOM 
        this.cclick = this.cclick+1;
        this.cstar= this.cstar+1;
        this .cshow = false;
        this .cshow1 = true;
        this.isActive_1 = false;
        this.isActive_2 = false;
        this.isActive_3 = true;
        this.isActive_4 = false;
      },
      show_4: function () {
        // `this`  Vue 

        // `event`  DOM 
        this.cclick = this.cclick+1;
        this.cstar= this.cstar+1;
        this .cshow = false;
        this .cshow1 = true;
        this.isActive_1 = false;
        this.isActive_2 = false;
        this.isActive_3 = false;
        this.isActive_4 = true;
      },
      show_5: function () {
        // `this`  Vue 

        // `event`  DOM 
        this.cclick = this.cclick+1;
        this.cstar= this.cstar+1;
        this .cshow = true;
        this .cshow1 = false;
        this.isActive_5 = true;
        this.isActive_6 = false;
        this.isActive_7 = false;
        this.isActive_8 = false;
      },
      show_6: function () {
        // `this`  Vue 

        // `event`  DOM 
        this.cclick = this.cclick+1;
        this.cstar= this.cstar+1;
        this .cshow = true;
        this .cshow1 = false;
        this.isActive_5 = false;
        this.isActive_6 = true;
        this.isActive_7 = false;
        this.isActive_8 = false;
      },
      show_7: function () {
        // `this`  Vue 

        // `event`  DOM 
        this.cclick = this.cclick+1;
        this.cstar= this.cstar+1;
        this .cshow = true;
        this .cshow1 = false;
        this.isActive_5 = false;
        this.isActive_6 = false;
        this.isActive_7 = true;
        this.isActive_8 = false;
      },
      show_8: function () {
        // `this`  Vue 

        // `event`  DOM 
        this.cclick = this.cclick+1;
        this.cstar= this.cstar+1;
        this .cshow = true;
        this .cshow1 = false;
        this.isActive_5 = false;
        this.isActive_6 = false;
        this.isActive_7 = false;
        this.isActive_8 = true;
      },
    }
  }
</script>



