<template>
  <div class="groupCompetition">
    <h3>-</h3>
    <ol v-for="Group in compGroups">
      <ul>
        <div class="Content">
          <div class="Logo pull-left">
            <img v-bind:src="Group.img">
          </div>
          <div class="pull-right">
            <li class="list-group-item glyphicon glyphicon-star-empty"> :{{Group.name}}</li>
            <li class="list-group-item glyphicon glyphicon-bookmark"> :{{Group.competition}}</li>
            <li class="list-group-item glyphicon glyphicon-calendar"> :{{Group.time}}</li>
            <li class="list-group-item glyphicon glyphicon-user"> :{{Group.originator}}</li>
            <li class="list-group-item glyphicon glyphicon-bullhorn"> :{{Group.memberNum}}</li>
            <li class="list-group-item glyphicon glyphicon-bell" v-if="Group.notEnough"> :{{Group.deadline}}</li>
          </div>
        </div>
      </ul>
    </ol>
  </div>
</template>


<script>
  export default {
    name: 'groupCompetition',
    data(){
      return{
        compGroups: [
          {
            img: './static/img/.png',
            name: ' ',
            competition: ' VRAR',
            time: '2017/12/25 13:00',
            originator: ' aoyuaWang',
            memberNum: ' 3/3',
            deadline: ' 2017/12/18',
            notEnough: false
           },
          {
            img: './static/img/.png',
            name: ' ',
            competition: ' ID@Xbox',
            time: ' 2017/12/8 18:00',
            originator: ' ',
            memberNum: ' 2/4',
            deadline: ' 2017/11/20',
            notEnough: true
          }
        ]
      }
    }
  }
</script>
<style>

.Content{
  margin:auto;
  bottom: 60px;
  right: 300px;
  width: 500px;
  height: 200px;
  text-align:left;
}
.Logo{
  margin: auto;
  position: relative;
  top: 20px;
  right: 10px;
  width: 100px;
  height: 120px;
}
.Logo img{
  display:block;
  outline:none;
  border:0;
  height:100%;
  width:100%;
}
</style>



