<template>
  <div class="groupActivities">
    <h3>-</h3>
    <ol v-for="Group in actGroups">
      <ul>
        <div class="Content">
          <div class="Logo pull-left">
            <img v-bind:src="Group.img">
          </div>
          <div class="pull-right">
            <li class="list-group-item glyphicon glyphicon-star-empty"> :{{Group.name}}</li>
            <li class="list-group-item glyphicon glyphicon-bookmark"> :{{Group.competition}}</li>
            <li class="list-group-item glyphicon glyphicon-calendar"> :{{Group.time}}</li>
            <li class="list-group-item glyphicon glyphicon-user"> :{{Group.originator}}</li>
            <li class="list-group-item glyphicon glyphicon-bullhorn"> :{{Group.memberNum}}</li>
            <li class="list-group-item glyphicon glyphicon-bell" v-if="Group.notEnough"> :{{Group.deadline}}</li>
          </div>
        </div>
      </ul>
    </ol>
  </div>
</template>


<script>
  export default {
    name: 'groupActivities',
    data (){
      return{
        actGroups: [
          {
            img: './static/img/1.png',
            name: ' ',
            competition: ' ',
            time: '2017/12/21 14:00',
            originator: ' ',
            memberNum: ' 3/20',
            deadline: ' 2017/12/20 23:00',
            notEnough: true
           },
          {
            img: './static/img/.png',
            name: ' ',
            competition: ' ',
            time: ' 2017/12/23 7:00',
            originator: ' ',
            memberNum: ' 65/100',
            deadline: ' 2017/12/23 0050',
            notEnough: true
          }
        ]
      }
    }
  }
</script>

<style>

.Content{
  margin:auto;
  bottom: 60px;
  right: 300px;
  width: 390px;
  height: 200px;
  text-align:left;
}
.Logo{
  margin: auto;
  position: relative;
  top: 20px;
  right: 10px;
  width: 90px;
  height: 140px;
}
.Logo img{
  display:block;
  outline:none;
  border:0;
  height:100%;
  width:100%;
}
</style>



