<template>
  <div class="grouppage">
    <h1></h1>
    <div class="container-fluid ">
      <div class="row-fluid">
        <div class="col-md-7">
          <h2>{{info.name}} - {{info.competition}}</h2>
          <img v-bind:src="info.img2" width="300px" height="220px">
          <p align="left">
            <br>
            
            <br><br>
            
            <br><br>
            2
            <br><br>
            Top5<br><br>
            <br>
            200-1000<br>
            646<br>
            <br>
            zhoushukai002<br>
          </p>
        </div>
        <div class="col-md-4 pull-right" align="left">
          <h2></h2>
          <ul>
            <li> :{{info.time}}</li><br>
            <li> :{{info.deadline}}</li><br>
            <li> :{{info.originator}}</li><br>
            <li> :{{info.memberNum}}</li><br>
            <li> {{info.place}}</li>
          </ul>
          <button class="btn" v-bind:class="{'btn-primary':notJoin, 'btn-default':join}" v-on:click="joinTeam">{{joinMsg}}</button>
          <button class="btn" v-bind:class="{'btn-success':notFollow, 'btn-default':follow}" v-on:click="Follow">{{followMsg}}</button>
        </div>
      </div>
      <div class="row-fluid">
        <div class="col-md-7">
          <h2></h2>
          <div v-for="com in Comments">
            <p class="list-group-item" align="left">{{com}}</p>
          </div>
          <div class="input-group">
              <input type="text" class="form-control" placeholder="" v-model="msg">
              <span class="input-group-btn">
                 <button class="btn btn-info" type="button" v-on:click="leaveMsg"></button>
              </span>
          </div>
        </div>
        <div class="col-md-4 pull-right">
          <group-recommend></group-recommend>
        </div>
      </div>
    </div>

  </div>
</template>


<script>
  import GroupRecommend from 'components/groups/groupRecommend/groupRecommend'
  export default {
    name: 'grouppage',
    components: {
        GroupRecommend
    },
    data(){
      return{
        info: {
          img: './static/img/1.png',
          img2: './static/img/.jpg',
          name: ' ',
          competition: ' ',
          time: '2017/12/21 14:00',
          originator: ' ',
          memberNum: ' 3/20',
          deadline: ' 2017/12/20 23:00',
          place: ' 848',
          intro: ''
        },
        Comments:["","emmmmQAQ"],
        msg:'',
        join: false,
        notJoin: true,
        follow: false,
        notFollow: true,
        joinMsg: '',
        followMsg: ''
      }
    },
    methods: {
      joinTeam: function(){
        this.join = !this.join
        this.notJoin = !this.notJoin
        if(this.join){
          this.joinMsg = ''
        }
        else{
          this.joinMsg = ''
        }
      },
      Follow: function(){
          this.follow = !this.follow
          this.notFollow = !this.notFollow
          if(this.follow){
            this.followMsg = ''
          }
          else{
            this.followMsg = ''
          }
      },
      leaveMsg: function(){
        if(this.msg != ''){
          this.Comments.push(this.msg)
        }
        this.msg=''
      }

    }

  }
</script>



